<template>
    <section>
        <public-item tips="xlsx"></public-item>
        <public-item title="ref" :code="code1">
            <input type="file" ref="myfile" accept=".xls,.xlsx" @change="changeFile" />
            <div>
                <div v-for="(item, index) in xlsxInfo.html" :key="index">
                    <div class="table_name"> {{ xlsxInfo.xlsxname }} - {{ index }}</div>
                    <div class="views-box" v-html="item"></div>
                </div>
            </div>
        </public-item>
        <public-item title="id" :code="code2">
            <input type="file" id="myfile" accept=".xls,.xlsx" @change="changeFileByID" />
            <div>
                <div v-for="(item, index) in xlsxInfoByID.html" :key="index">
                    <div class="table_name"> {{ xlsxInfoByID.xlsxname }} - {{ index }}</div>
                    <div class="views-box" v-html="item"></div>
                </div>
            </div>
        </public-item>
        <public-item title="" :code="code3">
            <input type="file" accept=".xls,.xlsx" @change="changeFileByBlod" />
            <div>
                <div v-for="(item, index) in xlsxInfoByBlod.html" :key="index">
                    <div class="table_name"> {{ xlsxInfoByBlod.xlsxname }} - {{ index }}</div>
                    <div class="views-box" v-html="item"></div>
                </div>
            </div>
        </public-item>
        <public-item title="" :code="code4">
            <div class="ipt-box">
                <el-input v-model="httpUrl" placeholder=""></el-input>
                <el-button @click="changeFileByHttp"></el-button>
            </div>
            <div>
                <div v-for="(item, index) in xlsxInfoByHttp.html" :key="index">
                    <div class="table_name"> {{ xlsxInfoByHttp.xlsxname }} - {{ index }}</div>
                    <div class="views-box" v-html="item"></div>
                </div>
            </div>
        </public-item>
        <public-item title="" :code="code5">
            <input type="file" accept=".xls,.xlsx" @change="changeFileByExport" />
            <div class="button-box">
                <el-button @click="outCvs"> cvs</el-button>
                <el-button @click="outXls"> xls</el-button>
                <el-button @click="outXlsx"> xlsx</el-button>
            </div>
        </public-item>
        <public-item title="Promise" :code="code6">
            <input type="file" accept=".xls,.xlsx" @change="changeFileByPromise" />
            <div>
                <div v-for="(item, index) in xlsxInfoByPromise.html" :key="index">
                    <div class="table_name"> {{ xlsxInfoByPromise.xlsxname }} - {{ index }}</div>
                    <div class="views-box" v-html="item"></div>
                </div>
            </div>
        </public-item>
        <public-item title="" :list="tableData"></public-item>
        <public-item title="" :list="returnTableData"></public-item>
        <public-item title="" :links="links"></public-item>
        <dzs-backtop  target=".el-main"></dzs-backtop>
    </section>
</template>
<script>
import readeExcel, { asyncReadeExcel } from "@/tool/readeExcel";
import {code1 , code2 , code3 , code4 , code5 , code6} from "./config.js";
import {tableData , returnTableData} from "./desc.js";
export default {
    data() {
        return {
            xlsxInfo: {},
            xlsxInfoByID: {},
            xlsxInfoByBlod: {},
            xlsxInfoByHttp: {},
            httpUrl: "https://anran233.com/QMX/  20230410.xlsx",
            xlsxInfoByExport: {},
            xlsxInfoByPromise: {},
            code1 : {
                title : "",
                code : code1,
                lang : "js"
            },
            code2 : {
                title : "",
                code : code2,
                lang : "js"
            },
            code3 : {
                title : "",
                code : code3,
                lang : "js"
            },
            code4 : {
                title : "",
                code : code4,
                lang : "js"
            },
            code5 : {
                title : "",
                code : code5,
                lang : "js"
            },
            code6 : {
                title : "",
                code : code6,
                lang : "js"
            },
            tableData ,
            returnTableData,
            links: {
                href: "https://github.com/zsduan/zsduan-summary/blob/master/src/tool/calender.js",
                title: ""
            },
        };
    },
    methods: {
        changeFile(e) {
            readeExcel({
                file: "myfile",
                Vue: this,
                success: (res) => {
                    this.xlsxInfo = res;
                },
            })
        },
        changeFileByID() {
            readeExcel({
                file: "myfile",
                success: (res) => {
                    this.xlsxInfoByID = res;
                },
            })
        },
        changeFileByBlod(e) {
            let file = e.target.files[0];
            readeExcel({
                file: file,
                success: (res) => {
                    this.xlsxInfoByBlod = res;
                },
            })
        },
        changeFileByHttp() {
            readeExcel({
                url: this.httpUrl,
                success: (res) => {
                    this.xlsxInfoByHttp = res;
                },
            })
        },
        changeFileByExport(e) {
            let file = e.target.files[0];
            readeExcel({
                file: file,
                success: (res) => {
                    this.xlsxInfoByExport = res;
                },
            })
        },
        /** cvs*/
        outCvs() {
            if (!this.xlsxInfoByExport.xlsxname) {
                this.$message.error("");
                return;
            }
            this.xlsxInfoByExport.outCvs();
        },
        /** xls*/
        outXls() {
            if (!this.xlsxInfoByExport.xlsxname) {
                this.$message.error("");
                return;
            }
            this.xlsxInfoByExport.outXls();
        },
        /** xlsx*/
        outXlsx() {
            if (!this.xlsxInfoByExport.xlsxname) {
                this.$message.error("");
                return;
            }
            this.xlsxInfoByExport.outXlsx();
        },
        changeFileByPromise(e) {
            asyncReadeExcel({
                file: e.target.files[0],
            }).then((res) => {
                this.xlsxInfoByPromise = res;
            })
        },
    }
};
</script>
<style lang="scss" scoped>
.ipt-box {
    display: flex;

    .el-button {
        margin-left: 10px;
    }
}

.button-box {
    margin-top: 10px;
}
</style>